import { Component, OnInit, ViewChild, ElementRef, SystemJsNgModuleLoaderConfig } from '@angular/core';
import { AccompanieService } from 'src/app/services/accompenie.service';
import { ActivatedRoute, Router } from '@angular/router';
import { DateHelper } from 'src/app/helpers/date-helper';

@Component({
  selector: 'app-exceptional-event',
  templateUrl: './exceptional-event.component.html',
  styleUrls: ['./exceptional-event.component.scss']
})
export class ExceptionalEventComponent implements OnInit {
  summaryPopup: boolean = false;
  remarkError: boolean;
  resonError: boolean;

  get options() {
    // console.log(this.accompSvc.accompenie.eventTypeList[0].text)
    return this.accompSvc.accompenie.eventTypeList;
  }
  set model(value) {
    this.accompSvc.accompenie = value;
  }


  constructor(private accompSvc: AccompanieService, private activityRoute: ActivatedRoute, private router: Router) { }

  reasonType: number = -1;
  remark: string;
  image: File;
  rideId: string;
  isFromTrip: string;
  img: any;
  showError: boolean;
  fileName: string;
  ngOnInit() {
    this.activityRoute.queryParams.subscribe(param => { this.rideId = param['id']; this.isFromTrip = param['isFromTrip']; });
  }


  selectedTypeEvent(e) {
    this.reasonType = e.value;
  }

  selectedimage(e) {

    this.accompSvc.loadData.next(true)
    var mimeType = e.target.files[0].type;
    if (mimeType.match(/image\/*/) == null) {
      this.showError = true;
      return;
    }
    this.showError = false;

    var reader = new FileReader();
    reader.readAsDataURL(e.target.files[0]);
    reader.onerror = (_event) => {
      // alert(reader.error)
    };

    reader.onload = (_event) => {
      this.fileName = e.target.files[0].name;
      var canvas = document.createElement("canvas");
      var img = document.createElement("img");
      img.src = reader.result as string;
      img.width = 300;
      img.height = 300;
      var ctx = canvas.getContext("2d");
      ctx.drawImage(img, 0, 0);
      canvas.width = img.width;
      canvas.height = img.height;
      var ctx = canvas.getContext("2d");

      setTimeout(() => {
        ctx.drawImage(img, 0, 0, img.width, img.height);
        this.img = canvas.toDataURL("image/png");
        this.accompSvc.loadData.next(false)
      }, 1000);
    }
    reader.readAsDataURL(e.target.files[0]);
  };

  saveEvent() {
    try {
      if (this.img != null) {
        if (this.img.split(',').length > 0)
          this.img = this.img.split(',')[1];
      }
    } catch (error) {
      console.log("123 " + error)
    }
    try {

      this.resonError = this.reasonType == null || this.reasonType == undefined || this.reasonType == -1 ? true : false;
      this.remarkError = this.reasonType == 100000000 && !this.remark ? true : false;

      if (this.remarkError || this.resonError) {
        return;
      }

      if (this.img != null || this.reasonType != null || (this.remark != "" && this.remark != null))
        this.accompSvc.accompenie.exceptionalEventList.push({ regardingId: this.rideId, caseType: this.reasonType, description: this.remark, image: this.img, fileName: this.fileName, startDate: DateHelper.getTodayFullDate() })
      this.img = null;
    } catch (error) {
      console.log("456 " + error)
    }
    try {
      this.model = this.accompSvc.accompenie;
    } catch (error) {
      console.log("678 " + error)
    }
    console.log("0");
    if (this.isFromTrip == 'false') {//אם הגיע לכאן מתוך מחוץ לנסיעה
      this.accompSvc.updateModelWithEmit(true, () => {
        this.summaryPopup = true;
      });
    }
    else {//אם הגיע מתוך נסיעה
      this.accompSvc.addItemToQueue(this.accompSvc.accompenie, true, true, () => this.summaryPopup = true);
    }


  }

  ApprovedAndReturnBackForm() {
    this.summaryPopup = false;
    if (this.isFromTrip == 'false') {//אם הגיע לכאן מתוך מחוץ לנסיעה
      this.goBack();//תחזור אחורה
    }
    else {//אם הגיע מתוך נסיעה
      this.router.navigate(['/my-trip', this.rideId]);//תחזור לנסיעה
    }
  }


  goBack() {
    history.go(-1)
  }


}

