import { Accompanie } from './../models/accompeanie';
import { Injectable } from '@angular/core';
import { Observable, Subject, throwError } from 'rxjs';
import { map, catchError } from 'rxjs/operators';
import { HttpClient } from '@angular/common/http';
import { HttpHeaders } from '@angular/common/http';

declare var NativeApp: any;

@Injectable()
export class ApiService {
  options = {};
  base_url = "/WebApi/";

  constructor(private http: HttpClient) {

    var httpHeaders = new HttpHeaders({
      'Content-Type': 'application/json'
    });
    this.options = httpHeaders;
    try {
      NativeApp.startTrip();
    } catch (error) {
    }
    this.base_url = "https://accompaniesapi20210712152846.azurewebsites.net/";//AZURE
    // this.base_url = "https://acconpaniestest.azurewebsites.net/";//AZURE TEST
    // this.base_url = "http://localhost:53388/";// Localhost
  }

  public post(url: string, obj: any): Observable<any> {
    return this.http.post(this.base_url + url, obj, this.options).pipe(
      map((res: string) =>
        JSON.parse(res)
      ),
      catchError(this.handleError),
    )
  }


  public get(url: string): Observable<any> {
    return this.http.get(this.base_url + url).pipe(
      map((res: any) =>
        res = res
      ),
      catchError(this.handleError),
    )
  }



  public postPromise(url: string, obj: any) {

    return this.http.post(this.base_url + url, obj, this.options).pipe(
      map((res: string) =>
        JSON.parse(res)
      ),
      catchError(this.handleError),
    ).toPromise()
  }

  private handleError(error: any) {
    console.log('Handling error locally and rethrowing it...', error);
    return throwError(error);
  }
}