import { Component, OnInit, OnDestroy, AfterViewInit, AfterViewChecked, ChangeDetectorRef } from '@angular/core';
import { AccompanieService } from './services/accompenie.service';
import { Router } from '@angular/router';
import { Subscription, interval } from 'rxjs';
import { LocationService } from './services/loaction.service';
import { first } from 'rxjs/operators';

declare var NativeApp: any;

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit, AfterViewChecked, OnDestroy {
  GPSDenied: Boolean;
  isLoad: boolean;
  subscription: Subscription;
  appStatus = interval(1000 * 1);
  appStatusInterval;;

  constructor(private locationSvc: LocationService, private accompSvc: AccompanieService, private router: Router, private cdRef: ChangeDetectorRef) {
    this.isLoad = true;

    try {
      NativeApp.WriteToAnalyticsOnAppIsOpen();
    } catch (error) { }

    if (!navigator.onLine) {
      this.router.navigate(['message-page/noInternet']);
    }
    this.accompSvc.checkAppVersion().subscribe((res) => {
      this.isLoad = false;
      if (res.details == 'True') {
        this.accompSvc = accompSvc;
        //alert(this.accompSvc.accompenie.smsOTP);
        if (this.accompSvc.accompenie != null && this.accompSvc.accompenie.smsOTP != null) {

          this.router.navigate(['my-trips']);
        } else
          this.router.navigate(['']);
      }
      else {
        this.router.navigate(['message-page/updateVersion']);
      }
    }, (err) => {
      this.isLoad = false;
      // this.accompSvc.openNoInternetAlert();
    });


    this.appStatusInterval = this.appStatus.subscribe(x => {

      var appStatusValue = (<HTMLInputElement>document.getElementById('appInForeground'));
      if (appStatusValue.value == 'true') {
        this.accompSvc.resetGPSCounter();
        (<HTMLInputElement>document.getElementById('appInForeground')).value="123";
      }
    });

  }
  ngOnDestroy(): void {
    this.appStatusInterval.unsubscribe();
  }

  ngOnInit() {
    this.subscription = this.accompSvc.getLoaddataStatus().subscribe(x => this.isLoad = x);
  }
  ngAfterViewChecked() {
    this.cdRef.detectChanges();
  }






}
