import { ChangeDetectorRef, Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { StudentValue } from 'src/app/models/accompeanie';
// import { BarcodeFormat } from '@zxing/library';
import { AccompanieService } from 'src/app/services/accompenie.service';
export * from '@zxing/ngx-scanner';

@Component({
  selector: 'app-search-student-scan-qrcode',
  templateUrl: './search-student-scan-qrcode.component.html',
  styleUrls: ['./search-student-scan-qrcode.component.scss']
})
export class SearchStudentScanQrcodeComponent implements OnInit {

  qrResultString: string = "empty";
  currentDevice: any = null;
  @Output() selectedStudent = new EventEmitter<any>();
  showScanQrcode: boolean = true;
  showApprovedStudent: boolean = false;
  errorAction: boolean = false;
  selectedStudentvalue: any;
  errorMessage: any;

  constructor(private accompSVC: AccompanieService) { }

  ngOnInit() {
    this.showScanQrcode = true;
    this.showApprovedStudent = false;
    // this.onCodeResult("{9AE499A9-11A6-E911-A829-000D3A3622E}");
  }

  onCodeResult(resultString: string) {
    // alert("onCodeResult");
    //alert(resultString);
    this.qrResultString = resultString;

    this.accompSVC.GetStudentBYIdFromQrCode(this.qrResultString).subscribe((res) => {
      this.selectedStudentvalue = {
        studentId: res.studentId,
        address: res.address,
        fullName: res.fullName,
        firstName: res.firstName,
        lastName: res.lastName
      };
      this.showScanQrcode = false;
      this.showApprovedStudent = true;
    }, (err) => {
      //alert(err.error.ExceptionMessage);
      this.errorAction = true;
      this.errorMessage = "לא נמצא תלמיד";
    });
  }
  clearResult(): void {
    this.qrResultString = null;
  }

  displayCameras(res: any) {
    // alert("displayCameras: " + res);
  }

  onHasPermission(param) {
    // alert("onHasPermission: " + param);
  }

  onCamerasFound(param) {
    // alert("onCamerasFound");
    // alert(param[0].deviceId);
    // alert(param[1].deviceId);
    this.currentDevice = param[0];
  }

  onCamerasNotFound(param) {
    // alert("onCamerasNotFound " + param);
  }

  onscanError(error) {
    // alert("onscanError: " + error);
    console.log(error);
  }

  approvedStudent() {
    this.showApprovedStudent = false;
    this.selectedStudent.emit(this.selectedStudentvalue);
  }

  DeselectStudent() {
    this.showApprovedStudent = false;
    this.showScanQrcode = true;
  }


  closePopup(student) {
    this.showApprovedStudent = false;
    this.showScanQrcode = false;
    this.errorAction = false;
    this.selectedStudent.emit();
  }

}

