import { DecimalPipe } from '@angular/common';
import { Location } from '../services/loaction.service';
import { BusinessUnit } from './BusinessUnit';

export class Accompanie {
    constructor(init?: Partial<Accompanie>) {
        Object.assign(this, init);
        this.trips = [];
    }
    id: string;
    // contactNeedsId: string;
    updateOnly: boolean;
    phone: string;
    firstName: string;
    lastName: string;
    tz: string;
    token: string;
    pushToken: string;
    smsOTP: string;
    fireBaseDeviceToken: string;
    isUpdateFireBaseDeviceToken: boolean;
    carTypeList: CarType[];
    trips: Trip[];
    notCollectedReasonList: NotCollectedReason[]
    rideReport: RideReport[];
    rideReportToUpdate: RideReport[];
    eventTypeList: EventType[];
    exceptionalEventList: ExceptionalEvent[];
    coordinator: Coordinator;
    businessUnit: BusinessUnit;
    currentLocation: RideReport;
    hasChanges: boolean;
    contactsNewRide: any;
    studentToAdd: studentInTrip;
    driver: Driver;
}


export class Trip {
    constructor() {
        this.students = [];
    }

    ID: string;
    date: Date | string | null;
    hour: any;
    endHour: any;
    startRide: Date | string | null;
    endRide: Date | string | null;
    latitudeStarting: number;
    longitudeStarting: number;
    latitudeEnding: number;
    longitudeEnding: number;
    rideName: string;
    carType: string;
    type: TripType;
    statuscode: number;
    students: Student[];
}
export class Student {
    rideforcontactId: string;
    id: string;
    contactNeedsId: string;
    name: string;
    studentAddress: string;
    address: string;
    addressLocation: Location;
    secondAddress: string;
    secondAddressLocation: Location
    fatherPhone: string;
    motherPhone: string;
    institution: _Account;
    secondInstitution: _Account;
    order: number;
    status: number;
    notColectedReason: number;
    statusDesc: string;
    hasSpecialPoint: boolean;

}
export class Driver {
    name: string;
    phone: string;
    carNumber: string;
}

export class RideReport {
    rideforContactId: string
    statusCode: number;
    longitude: number;
    latitude: number;
    date: Date | string | null;
    rideId: string
}

export class _Account {
    name: string;
    address: string;
}


export class CarType {
    id: string;
    name: string;
}

export class NotCollectedReason {
    text: string;
    value: number;
}

export class EventType {
    text: string;
    value: number;
}
export class ExceptionalEvent {
    regardingId: string;
    caseType: number;
    description: string;
    image: string;
    fileName: string;
    startDate: Date | string | null;
}

export class Coordinator {
    name: string;
    phone: string;
}




// export enum StudentStatus {
//     notCollected = 1, collected = 2, gotDown = 3, waiting = 4
// }
export enum TripType {
    collecting = 1, scattering = 2
}

export interface StudentValue {
    id: string;
    fullName: string;
    firstName: string;
    lastName: string;
    address: string;
}

export class NewTrip {
    name: string;
    accompany: string;
    // startHour: string;
    // date: Date;
}

export class studentInTrip {
    rideId: string;
    contactNeedsID: string;
    rideForContactId?: string;
    latitude: number;
    longitude: number;
    status: number;
}