export class DateHelper {
  public static getTodayDate() {
    var now = new Date();
    var dd = String(now.getDate()).padStart(2, '0');
    var mm = String(now.getMonth() + 1).padStart(2, '0');
    var yyyy = now.getFullYear();
    // console.log(yyyy + '-' + mm + '-' + dd);
    return yyyy + '-' + mm + '-' + dd;
  }
  public static getTodayFullDate() {
    var now = new Date();
    var dd = String(now.getDate()).padStart(2, '0');
    var mm = String(now.getMonth() + 1).padStart(2, '0');
    var yyyy = now.getFullYear();
    var ho = String(now.getHours()).padStart(2, '0');
    var mi = now.getMinutes().toString();
    if(mi.length == 1)
      mi = "0" + mi;

    return yyyy + '-' + mm + '-' + dd + 'T' + ho + ':' + mi + ':00Z';
  }


  // 2019-05-01T06:30:00Z

  public static getTodayDateShort() {
    var now = new Date();
    var dd = String(now.getDate());
    var mm = String(now.getMonth() + 1);
    var yyyy = now.getFullYear();
    return dd + '.' + mm + '.' + yyyy;
  }



}