import { LocationService } from './../../services/loaction.service';
import { Component, OnInit, AfterViewChecked, AfterViewInit, OnDestroy } from '@angular/core';
import { AccompanieService } from 'src/app/services/accompenie.service';
import { ActivatedRoute, Router } from '@angular/router';
import { FormsModule, ɵangular_packages_forms_forms_f } from '@angular/forms';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RideReport } from 'src/app/models/accompeanie';
import { DateHelper } from 'src/app/helpers/date-helper';
import { LocationComponent } from 'src/app/components/location/location.component';
import { interval } from 'rxjs';

declare var NativeApp: any;
@Component({
  selector: 'app-my-trip',
  templateUrl: './my-trip.component.html',
  styleUrls: ['./my-trip.component.scss']
})
export class MyTripComponent implements OnInit, OnDestroy {

  onSwipe: boolean
  startXswipe: number;
  popupSubject: number;
  popupSubjectClose: boolean;
  popupCancelAction: boolean;
  currentTrip: any;
  // indexTrip: number;
  indexStudent: string;
  cancelIndexStudent: string;

  studentList: any[] = [];
  studentListToUpdateStatus: any[] = [];
  totalCount: number;
  address: string;
  selectedReason: any;
  requiredReason: boolean = false;
  reasonDesc: string;
  status: any;
  iii: number = 0;
  tripId: string;
  updateStudentsStatusPopup: boolean = false;
  enableSchoolbus: boolean = false;
  offline: boolean = false;
  constructor(private locationSvc: LocationService, private accompSvc: AccompanieService, private activityRoute: ActivatedRoute, private router: Router, private locationcomp: LocationComponent) {

    interval(1000 * 1).subscribe(x => {
      this.offline = !navigator.onLine;;
      console.log(this.offline);
    });

    this.accompSvc.setRequestsQueue();

    /*schoolbus*/
    /*
    //for test
    var schoolbusObj = {
      "attendantID": "637b5a737e52e3b2d0fe8980",
      "teamID": "637b5abe7e52e3b2d0fe8984",
      "token": "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJhdHRlbmRhbnRhIjoiMTIzNDU2NzgyIiwiaWF0IjoxNjcxNTI1MDczLCJleHAiOjE2NzE2MTE0NzN9.a3-j8ugEiA2CZeDjvzJaYCzUClimArAW5z7FQxMUp7k"
    };
    this.enableSchoolbus = true;
    this.accompSvc.isSchoolbusTrip = this.enableSchoolbus;
    //NativeApp.SaveSchoolbusDataInWebView(schoolbusObj.attendantID, schoolbusObj.teamID, schoolbusObj.token);
    */


    //production
    this.accompSvc.getSchoolbusToken().subscribe((res: any) => {
      this.enableSchoolbus = res != null;
      // alert(this.enableSchoolbus);
      this.accompSvc.isSchoolbusTrip = this.enableSchoolbus;
      var schoolbusObj = JSON.parse(res);
      // alert(schoolbusObj.attendantID + " , " + schoolbusObj.teamID + " , " + schoolbusObj.token);
      NativeApp.SaveSchoolbusDataInWebView(schoolbusObj.attendantID, schoolbusObj.teamID, schoolbusObj.token);
    }, (err) => { alert("שגיאה מול סקולבס: " + err.error.ExceptionMessage) });

  }
  ngOnDestroy(): void {
  }

  openSchoolbusSite() {
    localStorage.setItem("lastRideId", this.currentTrip.ID);
    NativeApp.OpenSchoolbusSite();
  }

  set model(value) {
    this.accompSvc.accompenie = value;
  }
  get options() {
    return this.accompSvc.accompenie.notCollectedReasonList;
  }

  getDataFromAccompanySVC(tripId) {
    this.currentTrip = this.accompSvc.accompenie.trips.find(t => t.ID == tripId);
    this.studentList = this.currentTrip.students;
    //this.studentList.sort((a, b) => (a.order > b.order) ? 1 : -1)
    this.totalCount = this.studentList.filter(s => s.status == 100000000 || s.status == 1).length;
  }


  ngOnInit() {
    this.activityRoute.params.subscribe(param => {
      this.tripId = param['id'];
      this.rideId = param['id'];
      this.getDataFromAccompanySVC(this.tripId);
      if (this.currentTrip.type == 1) this.address = ' איסוף ל' + this.currentTrip.rideName; else this.address = ' פיזור ל' + this.currentTrip.rideName;
    }
    );
    if (this.studentList.length > 0) this.studentList.forEach(x => x["swipe"] = 0);
  }

  // notCollected = 1, collected = 2, gotDown = 3, waiting = 4

  onPanStart(event: any): void {
    this.startXswipe = event.center.x;
  }

  rideforContactId: string
  statusCode: number;
  longitude: number;
  latitude: number;
  DateTime: Date | string | null;
  rideId: string


  onPanEnd(event: any, index): void {
    var item = this.studentList[index];
    item.swipe = 0;

    if ((this.startXswipe - event.center.x) > 50) {
      this.updateStatus(index);
    }

  }

  updateStatus(index) {

    this.accompSvc.changeStudentStatus(index, this.accompSvc.accompenie.trips.find(t => t.ID == this.tripId));
    this.getDataFromAccompanySVC(this.tripId);
  }


  checkPrevStatus(prev, current) {
    switch (prev) {
      case 100000000: if (current == 100000001) return 'yes'
      else if (current == 1) return 'no'; break;  //collected
      case 100000001: return 'no'; break  //got down
      case 100000002: return 'no'; break//not collected
    }
  }

  changeStatus(index) {
    this.updateStatus(index);
  }

  changeStatusBack() {
    var student = this.accompSvc.accompenie.trips.find(t => t.ID == this.tripId).students[this.cancelIndexStudent];
    var item = this.studentList[this.cancelIndexStudent];
    this.status = item.status;
    switch (item.status) {
      case 100000002:
      case 100000000:
        item.status = 1;
        student.status = 1;
        break;
      case 100000001:
        item.status = 100000000;
        student.status = 100000000;
        break;
    }
    this.model = this.accompSvc.accompenie;

    var rideforContact = student.rideforcontactId;
    var status = student.status;

    this.accompSvc.createReport(this.rideId, rideforContact, status, this.status, true);
    this.getDataFromAccompanySVC(this.rideId);
    this.popupCancelAction = false;
  }




  // notCollected = 1, collected = 2, gotDown = 3, waiting = 4



  onPan(event: any, index): void {
    var item = this.studentList[index];
    if (event.center.x < this.startXswipe && item.status != 100000001) {
      item.swipe = this.startXswipe - event.center.x;
    }
  }

  getSelectedReason(e) {
    this.selectedReason = e.value;
    this.requiredReason = false;
  }
  notCollected() {
    if (!this.selectedReason) {
      this.requiredReason = true;
      return;
    }
    else if (this.selectedReason == 100000004 && !this.reasonDesc) {
      this.requiredReason = true;
      return;
    }
    var a = this.studentList[this.indexStudent];
    a.status = 100000002;

    this.accompSvc.accompenie.trips.find(t => t.ID == this.tripId).students[this.indexStudent].notColectedReason = this.selectedReason;
    this.accompSvc.accompenie.trips.find(t => t.ID == this.tripId).students[this.indexStudent].statusDesc = this.reasonDesc;
    var item = this.accompSvc.accompenie.trips.find(t => t.ID == this.tripId).students[this.indexStudent];

    this.accompSvc.accompenie.trips.find(t => t.ID == this.tripId).students[this.indexStudent].status = 100000002;

    var loc = this.locationSvc.getLocationForReport();

    this.accompSvc.accompenie.rideReport.push({ rideforContactId: item.rideforcontactId, statusCode: item.status, longitude: loc.lang, latitude: loc.lat, date: DateHelper.getTodayFullDate(), rideId: this.rideId })

    this.model = this.accompSvc.accompenie;

    this.popupSubject = null
    this.reasonDesc = null;
    // this.accompSvc.updateModelNoReturn(false);
    this.accompSvc.addItemToQueue(this.accompSvc.accompenie, false, false);
    this.totalCount = this.studentList.filter(s => s.status == 100000000 || s.status == 1).length;
    // window.location.reload();
    // this.createReport(this.rideId, "ghhg", status, true);
    this.clearNotCollectedFields();
  }
  clearNotCollectedFields() {
    this.selectedReason = undefined;
    this.reasonDesc = undefined;
  }

  checkAllStudentStatus() {
    this.studentListToUpdateStatus = this.studentList.filter(s => s.status != 100000001 && s.status != 100000002);
    if (this.studentListToUpdateStatus.length > 0) {
      this.updateStudentsStatusPopup = true;
    }
    else {
      this.updateStudentsStatusPopup = false;
      this.popupSubjectClose = true;
    }

  }

  //close trip
  closeTrip(e) {
    this.iii++;
    try {
      NativeApp.WriteToAnalyticsOnCloseRide();
    } catch (error) { }
    this.accompSvc.loadData.emit(true);
    this.accompSvc.stopTrackingTrip();

    var trip = this.accompSvc.accompenie.trips.find(t => t.ID == this.tripId);
    trip.endRide = DateHelper.getTodayFullDate();
    trip.statuscode = 3;

    var tmpLoc = this.locationSvc.getLocation(false);
    trip.latitudeEnding = tmpLoc.lastLocation.lat;
    trip.longitudeEnding = tmpLoc.lastLocation.lang;

    this.accompSvc.addItemToQueue(this.accompSvc.accompenie, true, false, () => {
      this.router.navigate(['/my-trips']);
      this.accompSvc.loadData.emit(false);
      this.accompSvc.stopQueueInterval();
    }, () => {
      this.router.navigate(['/my-trips']);
      this.accompSvc.loadData.emit(false);
    });

    /*this.accompSvc.updateModelWithEmit(false, () => {
      // this.popupSubjectClose = false;
      // this.model = this.accompSvc.accompenie;
      this.router.navigate(['/my-trips']);
      this.accompSvc.loadData.emit(false);
    }, () => {
      // this.popupSubjectClose = false;
      this.router.navigate(['/my-trips']);
      this.accompSvc.loadData.emit(false);
    });*/
  }

  //close trip
  closeTripSync(e) {

    this.closeTrip(e)
  }

  clickEvent() {
    this.router.navigate(['/exceptional-event'], { queryParams: { id: this.rideId, isFromTrip: true } })
  }



}
