import { MyTripsComponent } from './pages/my-trips/my-trips.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LogInComponent } from './pages/log-in/log-in.component';
import { MyTripComponent } from './pages/my-trip/my-trip.component';
import { SettingsComponent } from './pages/settings/settings.component';
import { PrevStartTripComponent } from './pages/prev-start-trip/prev-start-trip.component';
import { ExceptionalEventComponent } from './pages/exceptional-event/exceptional-event.component';
import { ContactUsComponent } from './pages/contact-us/contact-us.component';
import { LoadComponent } from './components/load/load.component';
import { MessagePageComponent } from './pages/message-page/message-page.component';

const routes: Routes = [
  { path: '', component: LogInComponent },
  { path: 'my-trips', component: MyTripsComponent },
  { path: 'my-trip/:id', component: MyTripComponent },
  { path: 'my-trip', component: MyTripComponent },
  { path: 'prev-start-trip/:id', component: PrevStartTripComponent },
  { path: 'prev-start-trip', component: PrevStartTripComponent },
  { path: 'settings', component: SettingsComponent },
  { path: 'exceptional-event/:id', component: ExceptionalEventComponent },
  { path: 'exceptional-event', component: ExceptionalEventComponent },
  { path: 'contact-us', component: ContactUsComponent },
  { path: 'load', component: LoadComponent },
  { path: 'message-page/:message', component: MessagePageComponent }

];

@NgModule({
  imports: [RouterModule.forRoot(routes,{useHash:true})],
  // 
  exports: [RouterModule]
})
export class AppRoutingModule { }
