import { Component, OnInit } from '@angular/core';
import { AccompanieService } from 'src/app/services/accompenie.service';

@Component({
  selector: 'app-contact-us',
  templateUrl: './contact-us.component.html',
  styleUrls: ['./contact-us.component.scss']
})
export class ContactUsComponent implements OnInit {

  constructor(private accompSvc: AccompanieService) { }



  get coordinator() {
    return this.accompSvc.accompenie.coordinator
  }


  get BusinessUnit () {
    return this.accompSvc.accompenie.businessUnit != (undefined || null) ? this.accompSvc.accompenie.businessUnit : undefined
  }



  ngOnInit() {
  }
  goBack() {
    history.go(-1)
  }

}
