import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { FormControl } from '@angular/forms';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable } from 'rxjs';
import { filter, map } from 'rxjs/operators';
import { DateHelper } from 'src/app/helpers/date-helper';
import { NewTrip, studentInTrip, StudentValue, Trip } from 'src/app/models/accompeanie';
import { FilterPipe } from 'src/app/pipes/filter.pipe';
import { AccompanieService } from 'src/app/services/accompenie.service';
import { LocationService } from 'src/app/services/loaction.service';

declare var NativeApp: any;

@Component({
  selector: 'app-search-student-buttons',
  templateUrl: './search-student-buttons.component.html',
  styleUrls: ['./search-student-buttons.component.scss']
})
export class SearchStudentButtonsComponent implements OnInit {
  errorAction: boolean;
  errorMessage: any;
  subscription: any;
  result: string = "empty";

  constructor(private dialog: MatDialog, public filterPipe: FilterPipe, public accompSvc: AccompanieService, private locationSvc: LocationService, private activityRoute: ActivatedRoute, private router: Router) { }
  qrcode: any;

  currentTrip: Trip;
  inTrip: boolean;//האם מגיעים מתוך מסך נסיעה או מתוך רשימת נסיעות
  rideId: string;
  popupSearchStudent: boolean;
  popupScanQrCode: boolean;
  autocompleteCtrl = new FormControl();
  filteredStudents: Observable<StudentValue[]>;
  studentsList: Array<StudentValue> = [];

  @Output() refreshParentData = new EventEmitter<any>();
  @Output() updateStudentListInTrip = new EventEmitter<any>();
  @Output() openRideToDriving = new EventEmitter<any>();
  @Output() stopRefreshing = new EventEmitter<any>();

  ngOnInit() {
    this.inTrip = this.activityRoute.snapshot.routeConfig.path == "my-trips" ? false : true;
    this.popupSearchStudent = false;
    this.popupScanQrCode = false;
  }

  openScanQR() {
    this.popupScanQrCode = true;
    if (!this.inTrip) {
      this.stopRefreshing.emit(true);
    }
  }

  openSearchStudentAutocomplete() {
    this.popupSearchStudent = true;
    if (!this.inTrip) {
      this.stopRefreshing.emit(true);
    }
  }

  closedPopup(selectedStudent) {

    this.popupSearchStudent = false;
    this.popupScanQrCode = false;
    if (selectedStudent != null) {
      this.onSelectedStudent(selectedStudent.studentId)
    }
    else if (!this.inTrip) {
      this.stopRefreshing.emit(false);
    }
  }


  onSelectedStudent(studentId: string) {

    if (navigator.onLine) {
      this.accompSvc.loadData.emit(true);
      this.currentTrip = null;
      //אם נמצא בתוך נסיעה - ישלוף את מזהה הנסיעה
      if (this.inTrip) {
        this.currentTrip = this.accompSvc.accompenie.trips.find(x => x.ID == this.activityRoute.snapshot.params["id"]);
      } else {
        let tripsToday = this.filterPipe.transform(this.accompSvc.accompenie.trips, "date", DateHelper.getTodayDate()).filter(x => x.statuscode != 3);
        if (tripsToday.length > 0) {
          let nowTime = new Date();
          var earlyTime = new Date(nowTime.getFullYear(), nowTime.getMonth(), nowTime.getDate(), nowTime.getHours(), nowTime.getMinutes() - 30, nowTime.getSeconds());
          var latestTime = new Date(nowTime.getFullYear(), nowTime.getMonth(), nowTime.getDate(), nowTime.getHours(), nowTime.getMinutes() + 30, nowTime.getSeconds());
          var nowHour = nowTime.getHours() * 100 + nowTime.getMinutes();
          ///נסיעות בטווח הקרוב
          var tripInThisHour = tripsToday.filter((trip) => {
            // let tripTime = parseInt(trip.hour.replace(":", ""))
            // return tripTime >= earlyTime.getHours() * 100 + earlyTime.getMinutes() && tripTime < latestTime.getHours() * 100 + latestTime.getMinutes() /*&& trip.statuscode == 1*/;
            let startTime = parseInt(trip.hour.replace(":", ""));
            let endTime = parseInt(trip.endHour.replace(":", ""));
            var earlyTime = new Date(nowTime.getFullYear(), nowTime.getMonth(), nowTime.getDate(), startTime / 100, startTime % 100 - 30, nowTime.getSeconds());
            var latestTime = new Date(nowTime.getFullYear(), nowTime.getMonth(), nowTime.getDate(), endTime / 100, endTime % 100 + 30, nowTime.getSeconds());
            return nowHour >= earlyTime.getHours() * 100 + earlyTime.getMinutes() && nowHour < latestTime.getHours() * 100 + latestTime.getMinutes() /*&& trip.statuscode == 1*/;
          });

          //לחפש תלמיד בשעה הקרובה שהתלמיד נמצא בה
          //אם לא מוצא נסיעה כזו - יקח את הנסיעה הראשונה המתאימה מבחינת השעות
          if (tripInThisHour.length > 0) {
            var tripWithStudent = tripInThisHour.find(trip => trip.students.some(x => x.contactNeedsId == studentId));
            this.currentTrip = tripWithStudent ? tripWithStudent : null;
          }
        }
      }

      //יצירת נסיעה
      if (this.currentTrip == null) {
        if (navigator.onLine) {
          this.accompSvc.createTrip().subscribe((res) => {
            if (res['error']) {
              this.accompSvc.loadData.emit(false);
              this.errorAction = true;
              this.errorMessage = res['error'];
              return;
            }
            this.accompSvc.accompenie.trips.push(res);
            this.currentTrip = res;
            this.accompSvc.currentRideID = res.ID;
            try {
              //התחלת דיווח לנסיעה
              this.openRideToDriving.emit();
            } catch (error) { }

            this.continueReoportStudents(res, true, studentId);
          });
        }
      }
      else {
        if (!this.inTrip) {
          //התחלת דיווח לנסיעה
          this.openRideToDriving.emit();
        }
        this.continueReoportStudents(this.currentTrip, false, studentId);
      }
    }
    else {
      this.errorAction = true;
      this.errorMessage = "לא ניתן להוסיף תלמיד כשאין רשת";
    }
  }

  continueReoportStudents(newTrip: Trip, isNewTrip, studentId?) {
    if (navigator.onLine) {
      this.currentTrip = newTrip;
      let isStudentExsist = this.currentTrip.students.find(x => x.contactNeedsId == studentId);
      var location = this.locationSvc.getLocationForReport();
      if (isStudentExsist) {
        //report
        this.createReportForStudent(isStudentExsist, location);
      }
      else {
        if (this.inTrip || isNewTrip) {
          let studentvar: studentInTrip = {
            contactNeedsID: studentId,
            latitude: location.lat,
            longitude: location.lang,
            rideId: this.currentTrip.ID,
            status: 1
          };
          this.accompSvc.accompenie.studentToAdd = studentvar;
          if (navigator.onLine) {
            this.accompSvc.addStudentToTrip().subscribe((res) => {
              this.accompSvc.accompenie.trips.find(x => x.ID == this.currentTrip.ID).students.push(res);
              this.updateStudentListInTrip.emit(this.currentTrip.ID);
              var student = this.accompSvc.accompenie.trips.find(x => x.ID == this.currentTrip.ID).students.find(x => x.contactNeedsId == studentId);
              this.currentTrip = this.accompSvc.accompenie.trips.find(x => x.ID == this.currentTrip.ID);
              this.createReportForStudent(student, location);
            },
              (err) => {
                this.accompSvc.loadData.emit(false);
                this.errorAction = true;
                this.errorMessage = err.error.ExceptionMessage;
              });
          }
        }
      }
    }
  }

  createReportForStudent(student, location) {

    for (var x of this.accompSvc.accompenie.trips) {
      if (x.ID == this.currentTrip.ID && x.statuscode != 2) {
        this.accompSvc.startTrackingTrip(this.currentTrip.ID);
        x.startRide = DateHelper.getTodayFullDate();

        var tmpLoc = this.locationSvc.getLocationForReport();
        x.latitudeStarting = tmpLoc.lat;
        x.longitudeStarting = tmpLoc.lang;

        x.statuscode = 2;
      }
    }

    let index = this.currentTrip.students.indexOf(student);
    this.accompSvc.changeStudentStatus(index, this.currentTrip);

    if (this.inTrip) {
      this.refreshParentData.emit(this.currentTrip.ID);
    }

    //task 5844 - hide car type page
    // if (!this.currentTrip.carType) {
    //   this.accompSvc.loadData.emit(false);
    //   this.router.navigate(['/prev-start-trip', this.currentTrip.ID]);
    // }
    // else if (!this.inTrip) {
    //   this.accompSvc.loadData.emit(false);
    //   this.router.navigate(['/my-trip', this.currentTrip.ID]);
    // }
    this.accompSvc.loadData.emit(false);
    this.router.navigate(['/my-trip', this.currentTrip.ID]);
    return;

  }

  closeErrorPopup() {
    this.errorMessage = "";
    this.errorAction = false;
  }

}


