import { AfterViewInit, Component, ElementRef, EventEmitter, OnInit, Output, ViewChild } from '@angular/core';
import { FormControl } from '@angular/forms';
import { Observable } from 'rxjs';
import { debounceTime, map, switchMap, tap } from 'rxjs/operators';
import { StudentValue } from 'src/app/models/accompeanie';
import { AccompanieService } from 'src/app/services/accompenie.service';

@Component({
  selector: 'app-search-student-autocomplete',
  templateUrl: './search-student-autocomplete.component.html',
  styleUrls: ['./search-student-autocomplete.component.scss']
})
export class SearchStudentAutocompleteComponent implements OnInit, AfterViewInit {
  autocompleteCtrl = new FormControl();
  @ViewChild("studentNameField") studentNameField: ElementRef;
  @Output() selectedStudent = new EventEmitter<any>();
  filteredStudents: Observable<StudentValue[]>;
  studentList: Array<StudentValue> = [];
  studentListTemp: Array<StudentValue> = [];
  firstNameStart: Array<StudentValue> = [];
  lastNameStart: Array<StudentValue> = [];
  firstNameInclude: Array<StudentValue> = [];
  lastNameInclude: Array<StudentValue> = [];
  fullNameEqual: Array<StudentValue> = [];
  fullNameInclude: Array<StudentValue> = [];
  showApprovedStudent: Boolean = false;
  selectedStudentValue: StudentValue;
  searchStudentField: string;
  loading: boolean = false;

  constructor(private accompSvc: AccompanieService) { }
  ngAfterViewInit(): void {
    // this.studentNameField.nativeElement.focus();
  }

  setFilteredStudentsList() {
  }

  ngOnInit() {

    this.autocompleteCtrl.valueChanges.pipe(
      debounceTime(200),
      switchMap((value: string) => {
        if (navigator.onLine) {
          if (typeof value === 'string') {
            this.searchStudentField = value;
            this.studentList = [];
            if (value.length < 2) {
              this.loading = false;
            }
            else {
              this.loading = true;
              return this.accompSvc.getStudentsForSearch(value);
            }
          }
        }
      })
    )
      .subscribe(res => {
        // console.log(res);
        // this.accompSvc.closeNoInternetAlert();
        this.studentList = [];
        this.fullNameEqual = [];
        this.fullNameInclude = [];
        this.firstNameStart = [];
        this.lastNameStart = [];
        this.firstNameInclude = [];
        this.lastNameInclude = [];
        if (res != null) {

          this.studentListTemp = res;

          this.studentListTemp.forEach((item, index) => {
            if (item.fullName == this.searchStudentField) {
              this.fullNameEqual.push(item);
            } else if (item.firstName.startsWith(this.searchStudentField)) {
              this.firstNameStart.push(item);
            }
            else if (item.lastName.startsWith(this.searchStudentField)) {
              this.lastNameStart.push(item);
            }
            else if (item.firstName.includes(this.searchStudentField)) {
              this.firstNameInclude.push(item);
            }
            else if (item.lastName.includes(this.searchStudentField)) {
              this.lastNameInclude.push(item);
            }
            else {
              this.fullNameInclude.push(item);
            }

          });

          this.studentList = this.fullNameEqual.concat(this.firstNameStart).concat(this.lastNameStart).concat(this.firstNameInclude).concat(this.lastNameInclude).concat(this.fullNameInclude);
        }
        this.loading = false;
      }, (err) => {
        this.loading = false;
        // this.accompSvc.openNoInternetAlert();
      });
  }

  getOptionText(option: StudentValue) {

    if (option) {
      return option.fullName;
    }
  }

  chooseStudent(student) {
    if (typeof student === 'string') {
      return;
    }
    this.selectedStudentValue = student;
    this.showApprovedStudent = true;

  }

  approvedStudent() {
    this.selectedStudent.emit(this.selectedStudentValue);
  }

  DeselectStudent() {
    this.showApprovedStudent = false;
  }

  closePopup(student) {

    this.selectedStudent.emit();
  }



}
