import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CarTypes, CarTypesCategory } from 'src/app/models/carTypes';
import { AccompanieService } from 'src/app/services/accompenie.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-car-type',
  templateUrl: './car-type.component.html',
  styleUrls: ['./car-type.component.scss']
})
export class CarTypeComponent implements OnInit {
  @Input() carType;
  carTypeName: string;
  carTypeList: CarTypes[];
  @Output() IsSelectedcarType: EventEmitter<any> = new EventEmitter();

  currentTripId: string;

  constructor(private accompSvc: AccompanieService, private activityRoute: ActivatedRoute, private router: Router) { }

  ngOnInit() {
    this.carTypeList = CarTypes.SetCarTypes().filter(c => CarTypesCategory[c.CarTypesCategory] == this.carType);
        this.carTypeName = this.carTypeList[0].CarName

  }

    
  radioChange(id, element) {
    if (document.getElementsByClassName("radioChecked").length > 0 ) {//אם כבר נבחר סוג רכב, 
      document.getElementsByClassName("radioChecked")[0].className = 
        document.getElementsByClassName("radioChecked")[0].className.replace("radioChecked", "")//תוריד את העיצוב מהבחירה הקודמת
    }

    element.currentTarget.parentElement.className += " radioChecked";//שים את העיצוב בבחירה החדשה

    this.IsSelectedcarType.emit(id);
    this.accompSvc.accompenie.trips.forEach(x => { if (x.ID == this.currentTripId) { x.carType = id; } })
  }

  

}
