import { Injectable } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Subscription, interval, Observable, Subject } from 'rxjs';
import { of } from 'rxjs';

declare var NativeApp: any;

@Injectable()
export class LogInService {

   public form: FormGroup;
   mySms: string;
   smsSubscription: Subscription;
   smsInterval = interval(1000);
   error: string;
   smsSubject: Subject<string> = new Subject();
   constructor() {
      this.form = new FormGroup({
         phone: new FormControl(null, Validators.required),
         tz: new FormControl(null)
      })
      //  NativeApp.startTrip();
   }

   autoCompleteSms() {
      try {
         NativeApp.startSmsListener();
      } catch (error) {
         //this.error = ("ERROR: " + error);

         // setTimeout(() => {
         //    (<HTMLInputElement>document.getElementById('smsFromApk')).value = '123456'; 
         //  }, 5000);
      }
      this.smsSubscription = this.smsInterval.subscribe((counter) => {
         var sms = (<HTMLInputElement>document.getElementById('smsFromApk'));
         //this.mySms = sms.value;
         if (sms.value) {
            var tempSms = sms.value;
            sms.value = "";
            this.smsSubscription.unsubscribe();
            this.smsSubject.next(tempSms)
         }
         //     if(counter >= 10){  
         //    this.smsSubscription.unsubscribe();
         //    this.smsSubject.next(null);
         // }   
      })


   }


}