import { Component, OnInit, Input, ViewChild, ElementRef, ViewChildren, QueryList, AfterViewInit } from '@angular/core';
import { interval, Subject, Subscription } from 'rxjs';

@Component({
  selector: 'app-split-input',
  templateUrl: './split-input.component.html',
  styleUrls: ['./split-input.component.scss']
})
export class SplitInputComponent implements OnInit, AfterViewInit {
  @Input() count: number;
  numberArray: number[];
  @ViewChild('hiddenInput') hiddenInput: ElementRef;
  @ViewChildren('inputs') inputs: QueryList<ElementRef>;
  @Input() automaticCode: string;

  smsSubscription: Subscription = new Subscription();
  smsInterval = interval(2000);
  //smsSubject: Subject<string> = new Subject();

  constructor() {
  }
  ngAfterViewInit(): void {

    // if (this.automaticCode) {
    //   this.setSMSCodeInInputs(this.automaticCode);
    // }

    this.smsSubscription = this.smsInterval.subscribe(() => {
      var sms = (<HTMLInputElement>document.getElementById('smsFromApk'));
      if (sms.value) {
        // alert(sms.value);
        this.setSMSCodeInInputs(sms.value);
        this.smsSubscription.unsubscribe();
      }
    });

  }

  ngOnInit() {
    this.numberArray = Array(this.count).fill(0).map((x, i) => i);
  }

  setSMSCodeInInputs(code) {
    // alert("setSMSCodeInInputs");
    let codesplit = code.match(/.{1,1}/g);
    this.inputs.forEach((item, index) => {
      item.nativeElement.value = codesplit[index];
    })
  }

  onChangeInput(index, event) {

    // console.log(index);
    if (index < this.count - 1 && event.keyCode != 8)
      this.inputs.toArray()[index + 1].nativeElement.focus();
    else if (event.keyCode == 8) {

    }
  }

  getValue() {
    var result = "";
    this.inputs.toArray().forEach(e => {
      result += e.nativeElement.value
    })
    return result;
  }

}
