import { LocationService } from './../../services/loaction.service';
import { LogInModel } from '../../models/log-in.model';
import { AccompanieService } from '../../services/accompenie.service';
import { Component, ViewChild, OnInit } from '@angular/core';
import { ApiService } from '../../services/api.service';
import { LogInService } from './log-in.service';
import { first } from 'rxjs/operators';
import { Accompanie } from 'src/app/models/accompeanie';
import { SplitInputComponent } from 'src/app/components/split-input/split-input.component';
import { Router } from '@angular/router';
import { Subscription, interval ,fromEvent, merge, of } from 'rxjs';
import { FormGroup } from '@angular/forms';
import { map } from 'rxjs/operators';


declare var NativeApp: any;

@Component({
  selector: 'app-log-in',
  templateUrl: './log-in.component.html',
  styleUrls: ['./log-in.component.scss'],
  providers: [LogInService]
})
export class LogInComponent {
  step: string;
  errorMsg: string;
  logInObj: LogInModel;
  subscription: Subscription;
  //online :string;

  load = false;
  @ViewChild(SplitInputComponent) smsCode: SplitInputComponent;
  code: any;

  get user() {
    return this.accompSvc.accompenie;
  }
  set user(value) {
    this.accompSvc.accompenie = value;
  }
  get form() {
    return this.svc.form;
  }
  get ctrls() {
    return this.form.controls;
  }

  get smsSubscription() {

    return this.svc.smsSubscription;
  }



  constructor(private svc: LogInService, private api: ApiService,
    private accompSvc: AccompanieService, private router: Router) {

    this.step = 'log-in';  //'pass' 
    
  }

  logIn(msgType) {
  if (navigator.onLine) 
  {
    this.load = true;

    this.logInObj = new LogInModel(this.form.value); // COMMENT FOR TEST
    this.logInObj.msgType = msgType;

    //TEST IT
    try {
      //NativeApp.startSmsListener();
    }
    catch {

    }

    this.api.post("user/login", this.logInObj)//.pipe(first())
      .subscribe((res: any) => {

        this.load = false;
        // this.accompSvc.closeNoInternetAlert();
        if (res.message == 'notExists')
          this.errorMsg = res.message;
        else if (res.message == 'success') {

          this.svc.autoCompleteSms();
          this.svc.smsSubject.pipe(first()).subscribe(code => {
            if (code)
              this.validteLogInCode(code)
          });
          this.step = 'pass';

        }
        else {
               //handle error
        }
      }, (err) => {
        this.load = false;
        // this.accompSvc.openNoInternetAlert();
      });
  } 
    
     
  }

  validteLogInCode(code: string) {
    //alert("validteLogInCode");
    var fireBaseToken = (<HTMLInputElement>document.getElementById('FireBaseToken'));
    if (fireBaseToken.value && fireBaseToken.value != "" && fireBaseToken.value != undefined) {
      this.logInObj.fireBaseDeviceToken = fireBaseToken.value;
    }
    this.load = true;
    this.logInObj.code = code;
    this.api.post("user/ValidateLogInCode", this.logInObj).pipe(first())
      .subscribe((res: any) => {
        // this.accompSvc.closeNoInternetAlert();
        if (res.message == 'incoorectPassword') {
          this.errorMsg = res.message;
          this.load = false;
        }
        else {
          res.smsOTP = code;
          this.user = res;
          this.accompSvc.needToGetDataFromAPI = false;

          this.accompSvc.checkAppVersion().subscribe((res) => {
            this.load = false;
            // this.accompSvc.closeNoInternetAlert();
            if (res.details == 'True') {
              this.router.navigate(['my-trips']);
            }
            else {
              this.router.navigate(['message-page/updateVersion']);
            }
          }, (err) => {
            this.load = false;
            // this.accompSvc.openNoInternetAlert();
          });
        }
      }, (err) => {
        this.load = false;
        // this.accompSvc.openNoInternetAlert();
      });

  }

}



