import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { AccompanieService } from 'src/app/services/accompenie.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.scss']
})
export class SettingsComponent implements OnInit {
  popupSubject: string;
  @Output() loadData: EventEmitter<any> = new EventEmitter();

  constructor(public accompSvc: AccompanieService, private router: Router) {
  }

  fullName: string;


  ngOnInit() {
    this.accompSvc.loadData.emit(false);
    this.fullName = this.accompSvc.accompenie.firstName + " " + this.accompSvc.accompenie.lastName;
  }

  logout() {
    this.accompSvc.logout();
    this.router.navigate(['/']);
  }

  logoutOffline() {
    this.accompSvc.logout();
    this.router.navigate(['/']);
  }


}
