import { Component, OnInit, Pipe, PipeTransform } from '@angular/core';
import { CarType } from 'src/app/models/accompeanie';
import { AccompanieService } from 'src/app/services/accompenie.service';
import { CarTypes, CarTypesCategory } from 'src/app/models/carTypes';
import { ActivatedRoute, Router } from '@angular/router';
import { Subscription } from 'rxjs';



@Component({
  selector: 'app-prev-start-trip',
  templateUrl: './prev-start-trip.component.html',
  styleUrls: ['./prev-start-trip.component.scss']
})


export class PrevStartTripComponent implements OnInit {
  step: string;
  headerText: string;
  CarTypeGuideID: string;
  IsSelectedcarType: boolean = false;;
  currentTripId: string;
  carTypeList: CarTypes[];
  subscription: Subscription;

  constructor(private accompSvc: AccompanieService, private activityRoute: ActivatedRoute, private router: Router) {
    this.step = "choose-bas";
    this.headerText = "נסיעה שלי";
  }
  get CarTypesCategoryList() {
    return CarTypesCategory;
  }
  // get carsList() {
  //   return this.accompSvc.accompenie.carTypeList;
  // }
  ngOnInit() {
    this.activityRoute.params.subscribe(param =>
      this.currentTripId = param['id']
    );

  }

  set model(value) {
    this.accompSvc.accompenie = value;
  }

  OnSelectedCarType(id) {
    this.IsSelectedcarType = true;
    //this.CarTypeGuideID = id;
    this.accompSvc.accompenie.trips.forEach(x => { if (x.ID == this.currentTripId) { x.carType = id; } })

  }
  selectedCarType() {

    this.model = this.accompSvc.accompenie;
    this.accompSvc.updateModelWithEmit();
    this.router.navigate(['/my-trip', this.currentTripId])
  }

}

@Pipe({
  name: 'enumToArray'
})
export class EnumToArrayPipe implements PipeTransform {
  transform(data: Object) {
    const keys = Object.keys(data);
    return keys.slice(keys.length / 2);
  }
}
